#overlay {
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

#modal {
    background: white;
    width: 90%;
    height: 90%;
    padding: 1rem;
    border: 2px #ccc solid;
    position: relative;
    margin: 0 auto;
    z-index: 200;
    top: 50%;
    transform: translateY(-50%);
    overflow: visible;
    overflow-y: scroll;
}

#modal .img-container {
    width: 300px;
    height: 400px;
    margin: 0 auto;
}

#modal img {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

#modal em {
    font-size: 0.8rem;
    color: var(--light);
}

#close {
    padding: 10px;
    height: auto;
}

#close svg {
    display: block;
    width: 20px;
    height: 20px;
}