#app {
	text-align: center;
	position: relative;
}

.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	word-wrap: normal !important;
}

/* https://loading.io/css/ */
#loading-container,
#modal-loading-container {
	position: absolute;
	z-index: 1;
	width: 100%;
	height: 100%;
	background-color: #fff;
	top: 0;
	left: 0;
	visibility: hidden;
}

#heart-loading {
	margin-top: 50px;
	display: inline-block;
	width: 80px;
	height: 80px;
	transform: rotate(45deg);
	transform-origin: 40px 40px;
}
	#heart-loading div {
		top: 32px;
		left: 32px;
		position: absolute;
		width: 32px;
		height: 32px;
		background: #df6adf;
		animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
	}

	#heart-loading div:after,
	#heart-loading div:before {
		content: " ";
		position: absolute;
		display: block;
		width: 32px;
		height: 32px;
		background: #df6adf;
	}

	#heart-loading div:before {
		left: -24px;
		border-radius: 50% 0 0 50%;
	}

	#heart-loading div:after{
		top: -24px;
		border-radius: 50% 50% 0 0;
	}

@keyframes lds-heart {
	0% {
		transform: scale(0.95);
	}
	5% {
		transform: scale(1.1);
	}
	39% {
		transform: scale(0.85);
	}
	45% {
		transform: scale(1);
	}
	60% {
		transform: scale(0.95);
	}
	100% {
		transform: scale(0.9);
	}
}
  
