.lightbox {
    --auto-grid-min-size: 200px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(var(--auto-grid-min-size), 1fr));
    grid-gap: 20px;
    margin-bottom: 2rem;
    grid-auto-rows: auto;
    justify-content: center;
}

.lightbox .thumbnail {
    cursor: pointer;
}

.lightbox .thumbnail:focus,
.lightbox .thumbnail:active,
.lightbox .thumbnail:hover {
    outline: none;
    filter: drop-shadow(0 0 4mm rgb(207, 140, 200));
}

.lightbox .thumbnail:hover img,
.lightbox .thumbnail:focus img,
.lightbox .thumbnail:active img {
    transform: scale(1.5);
    filter: drop-shadow(0 -6mm 4mm rgb(160, 0, 210));
}

.lightbox .thumbnail .img-container {
    position: relative;
    overflow: hidden;
}

.lightbox .thumbnail h6 {
    margin: 0;
    font-weight: bold;
}

.lightbox .thumbnail .img-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform .2s;
}